import React from 'react';
import './item.css';

const Item = (props) => {
    let classString = 'item-name';
    if(props.important){
        classString += 'important';
    }
    if(props.like){
        classString +='like';
    }
    return(
        <div className="item">
            <span className={classString}>{props.name}</span>
            <div className="button-bar">
                <i className="item-done-button">ok</i>
                <i className="item-delete-button" onClick={props.deleteHandler}>x</i>
            </div>
            
        </div>
    )
    
}

export default Item;