import React from 'react';
import './add-panel.css';

const AddPanel = ({add}) => {
    return (
        <form className="add-panel">
                <input className="add-info" type="text"></input>
                <button className="add-button" type="submit" onClick={(e) => add(e,'Сделать квас')}>ADD</button>
        </form>
    )
}
export default AddPanel;