import React from 'react';
import './App.css';

import Header from '../header/header';
import ItemList from '../item-list/item-list';
import AddPanel from '../add-panel/add-panel';

export default class App extends React.Component {
    constructor(props){
		super(props);
		this.state = {
			data:[
			{
				name:'Сделать проект',
				important:false,
				like:false,
				id:1
			},{
				name:'Выгулять пса',
				important:false,
				like:false,
				id:2
			},{
				name:'Позвонить другу',
				important:false,
				like:false,
				id:3
			}
			]
		}
		this.deleteHandler = this.deleteHandler.bind(this);
		this.addHandler = this.addHandler.bind(this);
    }
    deleteHandler(id){
      	this.setState(({data}) => {
			  const index = data.findIndex(elem => elem.id === id)
			  const newArr = [...data.slice(0,index), ...data.slice(index+1)];

			  return {
				data:newArr
			  }
		  })
    }
	addHandler(event,string){
		event.preventDefault();
		this.setState(({data}) => {
			console.log(data[data.length-1].id + 1)
			const newArr = [...data,{
				name:string,
				important:false,
				like:false,
				id:data[data.length-1].id + 1
			}]
			return {
				data:newArr
			}
		})
	}
    render () {
		return(
			<div className="container">
				<Header></Header>
				<AddPanel add={this.addHandler}></AddPanel>
				<ItemList info={this.state.data} deleteHandler={this.deleteHandler}></ItemList>
			</div>
		)
    }
    
}